<template>
  <Modal
    :active="showModal"
    :event-name="'change-event-date-modal'"
    :custom-padding="'p-spacing-0'"
  >
    <div class="flex h-full min-h-[400px] w-full flex-col">
      <GeneralForm
        :heading="'CHANGE EVENT DATE'"
        :sub-heading="'Enter the new event date'"
        :show-submit-button="true"
        :submit-button-label="'Confirm'"
        @close-pressed="handleClose"
        @form-submitted="handleSubmit"
        :is-form-submitting="isFormSubmitting"
        class="flex-1"
      >
        <template #form-body>
          <FormInput
            label="New Date"
            ref="newEventDate"
            v-model="formData.newEventDate"
            :field-validations="fieldValidations?.newEventDate"
            input-type="date"
            inputId="newEventDate"
            class="mb-spacing-100"
          />
        </template>
      </GeneralForm>
    </div>
  </Modal>
</template>

<script>
import Modal from "@/components/General/Modal.vue";
import { FormValidationRule } from "@/utilities/helpers/formValidations";
import { getDateFromToday } from "@/utilities/helpers/utils";
import axiosInstance from "@/axiosInstance";
export default {
  components: {
    Modal,
    GeneralForm: () => import("@/components/General/Form.vue"),
    FormInput: () => import("@/components/General/FormInput.vue"),
  },
  data() {
    return {
      showModal: false,
      event: null,
      formData: {
        newEventDate: "",
      },
      fieldValidations: {
        newEventDate: new FormValidationRule({
          type: "DATE",
          required: true,
          minDate: getDateFromToday(1),
          maxDate: getDateFromToday(30),
        }),
      },
      isFormSubmitting: false,
    };
  },
  mounted() {
    this.$bus.$on("change-event-date-modal", (data) => {
      if (data?.showModal) {
        this.showModal = data?.showModal;
        this.event = data?.event;
      } else {
        this.showModal = false;
        this.event = null;
      }
    });
  },
  methods: {
    handleClose() {
      this.$bus.$emit("change-event-date-modal", false);
    },
    async handleSubmit() {
      const isValid = this.$validateForm();
      if (!isValid) {
        return;
      }
      this.isFormSubmitting = true;
      const payload = {
        id: this.event?._id,
        body: { newEventDate: this.formData.newEventDate },
      };
      const res = await axiosInstance.patch(
        `/calendar/events/${payload?.id}`,
        payload?.body,
      );
      if (res?.data?.success) {
        this.$toast.success(res?.data?.message);
        this.$bus.$emit("refetch-today-events");
        this.handleClose();
      } else {
        this.$toast.error(res?.message);
      }
      this.isFormSubmitting = false;
    },
  },
  beforeDestroy() {
    this.$bus.$off("change-event-date-modal");
  },
};
</script>
